/*
0 to 600px (37.5em) Phone only
600 to 900px        Table portrait
900 - 1200px        Tablet landscape
[1200 - 1800px] is where our normal styles apply 
1800px + :          Big desktop

$breakpoint argument choices:
-phone
-tab-port
-tab-land
-big-desktop

ORDER: Base + typography > general + grid > page layout > components

1em = 16px
*/
.btn,
.btn:link,
.btn:visited {
  font-size: 1.9rem;
  color: #fff;
  text-decoration: none;
  padding: 1.5rem 2.8rem;
  border-radius: 3px;
  background-color: rgba(231, 76, 60, 0.7);
  transition: all 0.2s;
}

.btn:hover {
  background-color: rgba(201, 61, 46, 0.9);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}

.btn:active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.btn-phone:link,
.btn-phone:visited {
  color: inherit;
  text-decoration: none;
  padding: 0.5;
}

.btn-phone:hover,
.btn-phone:focus {
  color: #f3cc2f;
}

.btn-phone:active {
  transform: translateY(1px);
}

.header__scroll_arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fade_move_down 3s ease-in-out infinite 1.1s;
}

.btn--animated-arrow {
  position: relative;
  border: 1px solid #fff;
  width: 5.5rem;
  height: 5.5rem;
  border-radius: 50%;
  opacity: 1;
}
.btn--animated-arrow.arrow-up span,
.btn--animated-arrow.arrow-down span {
  z-index: 999;
  height: 2px;
  margin: 1px;
  width: 1.5rem;
  background: #fff;
  transition: 0.4s ease;
  display: block;
  position: absolute;
}
.btn--animated-arrow.arrow-up span:first-child {
  left: 25%;
  bottom: 35%;
  transform: rotate(135deg);
}
.btn--animated-arrow.arrow-up span:nth-child(2) {
  left: 45%;
  bottom: 35%;
  transform: rotate(-135deg);
}
.btn--animated-arrow.arrow-up span:nth-child(3) {
  left: 25%;
  bottom: 54%;
  transform: rotate(135deg);
}
.btn--animated-arrow.arrow-up span:nth-child(4) {
  left: 45%;
  bottom: 54%;
  transform: rotate(-135deg);
}
.btn--animated-arrow.arrow-down > span:first-child {
  left: 25%;
  bottom: 35%;
  transform: rotate(45deg);
}
.btn--animated-arrow.arrow-down span:nth-child(2) {
  left: 45%;
  bottom: 35%;
  transform: rotate(-45deg);
}
.btn--animated-arrow.arrow-down span:nth-child(3) {
  left: 25%;
  bottom: 54%;
  transform: rotate(45deg);
}
.btn--animated-arrow.arrow-down span:nth-child(4) {
  left: 45%;
  bottom: 54%;
  transform: rotate(-45deg);
}

.btn--animated-arrow.arrow-down:hover span:nth-child(1) {
  transform: rotate(-135deg);
}

.btn--animated-arrow.arrow-down:hover span:nth-child(2) {
  transform: rotate(135deg);
}

.btn--animated-arrow.arrow-down:hover span:nth-child(3) {
  transform: rotate(225deg);
}

.btn--animated-arrow.arrow-down:hover span:nth-child(4) {
  transform: rotate(-225deg);
}

.btn--animated-arrow.arrow-up:hover span:nth-child(1) {
  transform: rotate(-45deg);
}

.btn--animated-arrow.arrow-up:hover span:nth-child(2) {
  transform: rotate(45deg);
}

.btn--animated-arrow.arrow-up:hover span:nth-child(3) {
  transform: rotate(-45deg);
}

.btn--animated-arrow.arrow-up:hover span:nth-child(4) {
  transform: rotate(45deg);
}

.header__scroll_arrow:hover {
  transition: all 0.3s;
  animation-play-state: paused;
}
.header__scroll_arrow:hover .btn--animated-arrow {
  background-color: rgba(201, 61, 46, 0.9);
  border: #c93d2e;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

