/* Sticky Nav */
div.sticky {
  padding: 0rem 0 0 8rem;
  width: 100%;
  position: fixed;
  /* nav will stay at the same point with respect to our scrooling */
  top: 0;
  left: 0;
  background-color: rgba(254, 254, 254, 0.98);
  box-shadow: 0 0.8px 1.5px rgba(255, 255, 255, 0.88);
  z-index: 9999;
  /* nav is the highest vertical stacking of all elements*/
}
div.sticky .logo > .logo__box {
  border: none;
  box-shadow: none;
  height: 6.4rem;
}
div.sticky .nav {
  padding: 0.3rem 0;
}
div.sticky .nav__menu {
  margin-top: 1rem;
}
div.sticky .nav__menu li a:link,
div.sticky .nav__menu li a:visited {
  color: #333;
}
div.sticky .nav__telephone i,
div.sticky .nav__telephone a:link,
div.sticky .nav__telephone a:visited {
  color: #333;
}
div.sticky .nav__telephone:hover .btn-phone,
div.sticky .nav__telephone:hover i {
  box-shadow: none;
  color: #f3cc2f;
}
div.sticky .nav__hours-link {
  color: #333;
}
div.sticky .nav__hours-link:hover {
  color: #f3cc2f;
}

