    right: 0;
  }
  .footer__title:nth-child(2) {
    margin-left: 0rem;
  }
  .footer__content--link p {
    line-height: 1.35;
  }
  .footer .footer__copyright {
    margin-bottom: 0rem;
  }
  .footer .col-1-of-3:not(:nth-child(2)) .footer__content {
    transform: translateX(20%);
  }
  .footer .col-1-of-3:nth-child(2) .footer__content {
