.header {
  position: relative;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5)),
    url(../img/sushi-hero.jpg);
  background-size: cover;
  background-position: center;
  /*MEDIA QUERIES FOR HEDAER*/
}
.header .header__nav {
  float: right;
}
.header .header__nav::after {
  content: '';
  display: table;
  clear: both;
}
.header__nav-box {
  padding: 6rem 0 0 8rem;
  width: 90%;
  margin: 0 auto;
  animation: moveInTop 1s ease-out;
}
.header__nav-box .logo {
  float: left;
}
.header .mobile_navi {
  display: none;
}
.header__content {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 90%;
  animation: moveInBottom 1s ease-out;
  -webkit-animation: moveInBottom 1s ease-out;
}
.header__scroll_arrow {
  position: absolute;
  bottom: 10%;
  left: 48.2%;
}
@media only screen and (max-width: 56.25em) {
  .header__scroll_arrow {
    left: 43%;
    bottom: 15%;
  }
}
@media only screen and (max-width: 81.25em) {
  .header__scroll_arrow {
    left: 47%;
    bottom: 15%;
  }
}
@media only screen and (max-width: 81.25em) {
  .header .mobile_navi {
    display: block;
  }
  .header__nav {
    display: none;
  }
}
@media only screen and (max-width: 56.25em) {
  .header__nav-box {
    width: 100%;
    padding: 4.2rem 0 0 3rem;
  }
  .header__nav {
    display: none;
  }
}

.mobile_navi__checkbox {
  display: none;
}

.mobile_navi__button {
  background-color: #fff;
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
  position: fixed;
  top: 6rem;
  right: 6rem;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
}
@media only screen and (max-width: 56.25em) {
  .mobile_navi__button {
    top: 4rem;
    right: 4rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .mobile_navi__button {
    top: 3rem;
    right: 3rem;
  }
}

.mobile_navi__background {
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  position: fixed;
  top: 6.5rem;
  right: 6.5rem;
  background-image: radial-gradient(#e74c3c, #c93d2e);
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
@media only screen and (max-width: 56.25em) {
  .mobile_navi__background {
    top: 4.5rem;
    right: 4.5rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .mobile_navi__background {
    top: 3.5rem;
    right: 3.5rem;
  }
}

.mobile_navi__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  visibility: hidden;
  opacity: 0;
  width: 0%;
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.mobile_navi__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  width: 100%;
}

.mobile_navi__item {
  margin: 1rem;
}

.mobile_navi__link:link,
.mobile_navi__link:visited {
  display: inline-block;
  font-size: 3rem;
  font-weight: 300;
  color: #fff;
  padding: 1rem 2rem;
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(
    120deg,
    transparent 50%,
    transparent 50%,
    #fff 50%
  );
  background-size: 250%;
  transition: all 0.4s;
}
.mobile_navi__link:link span,
.mobile_navi__link:visited span {
  margin-right: 1.5rem;
  display: inline-block;
}

.mobile_navi__link:hover,
.mobile_navi__link:active {
  background-position: 100%;
  color: #e53e2d;
  transform: translateX(1rem);
}

.mobile_navi__checkbox:checked ~ .mobile_navi__background {
  transform: scale(80);
}

.mobile_navi__checkbox:checked ~ .mobile_navi__nav {
  visibility: visible;
  opacity: 1;
  width: 100%;
}

.mobile_navi__checkbox:checked + .mobile_navi__button .mobile_navi__icon {
  background-color: transparent;
}

.mobile_navi__checkbox:checked
  + .mobile_navi__button
  .mobile_navi__icon::before {
  top: 0;
  transform: rotate(135deg);
}

.mobile_navi__checkbox:checked
  + .mobile_navi__button
  .mobile_navi__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

.mobile_navi__icon {
  position: relative;
  margin-top: 3.5rem;
}
.mobile_navi__icon,
.mobile_navi__icon::before,
.mobile_navi__icon::after {
  width: 3rem;
  height: 2px;
  background-color: #999;
  display: inline-block;
  transition: all 0.2s;
}
.mobile_navi__icon::before,
.mobile_navi__icon:after {
  content: '';
  position: absolute;
  left: 0;
}
.mobile_navi__icon::before {
  top: -0.8rem;
}
.mobile_navi__icon::after {
  top: 0.8rem;
}

.mobile_navi__button:hover .mobile_navi__icon::before {
  top: -1rem;
}

.mobile_navi__button:hover .mobile_navi__icon::after {
  top: 1rem;
}

